@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@600&display=swap);
#root {
    box-sizing: border-box;
    background: rgb(58,177,180);
    background: linear-gradient(313deg, rgba(58,177,180,1) 0%, rgba(29,79,253,1) 0%, rgba(252,121,69,1) 100%);
}


* {
    box-sizing: border-box;
}

.card {
    border: 2px solid greenyellow;
    border-radius: 10px;
    width: 250px;
    margin: 10px;
    transition: all ease-in-out .3s;
}

.card:hover {
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
    cursor: pointer;
}

.pokemonImage {
    width: 235px;
    height: 235px;
}

.pokemonName {
    font-family: Cinzel, serif;
    text-align: center;
    position: relative;
}

.pokemonName::after {
    position: absolute;
    content: '';
    height: 2px;
    bottom: -8px;
    margin: 0 auto;
    left: 0;
    right: 0;
    width: 50%;
    background: orange;
}

.pokemonHP {
    font-family: Cinzel, serif;
    text-align: center;
}

.pokemonInfo {
    display: flex;
    color: white;
    text-align: center;
}

.infoElement {
    font-family: 'Cinzel', serif;
    font-size: 14px;
    list-style: none;
    width: 28%;
    font-size: 12px;
}


body {
    margin: 0;

}

.cards {
    display: flex;
    padding: 20px;
    flex-wrap: wrap;
    justify-content: center;
}

.pokemonLogo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
    padding-top: 50px;
    padding-bottom: 20px ;
}

.navigation {
    text-align: center;
}

.navButton {
    width: 150px;
    height: 45px;
    border: 2px solid orange;
    border-radius: 10px;
    background-color: red;
    color: white;
    margin: 5px;
}

.navButton:hover {
    background-color: orange;
    border: 2px solid red;
    color: black;
    cursor: pointer;
}

button:focus {outline:0;}
